.button {
  background: linear-gradient(180deg, rgba(14, 164, 155, 1) 0%, rgba(5, 62, 59, 1) 100%);
  border: none;
  border-radius: 100px;
  padding: 12px 24px;
  width: 290px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
  display: flex;
  justify-content: center;
  align-items: center;
}
.button:hover {
  background: linear-gradient(180deg, rgba(20, 180, 170, 1) 0%, rgba(10, 70, 65, 1) 100%);
  transform: scale(1.05);
}
.button:active {
  transform: scale(0.95);
}
.button .text {
  color: white;
  font-family: "Poppins-Medium", sans-serif;
  font-size: 20px;
  font-weight: 500;
}
.text {
  color: #ffffff;
  text-align: center;
  font-family: "Poppins-Medium", sans-serif;
  font-size: 20px;
  font-weight: 500;
  position: relative;
  width: 460px;
