}.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  width: 100%;
}
.form-group {
  position: relative;
  width: 100%;
  max-width: 371px;
  height: 56px;
}
.input-wrapper {
  position: relative;
  width: 100%;
}
input {
  width: 100%;
  padding: 12px 8px;
  font-family: "DmSans-Regular", sans-serif;
  font-size: 18px;
  border: none;
  border-bottom: 1px solid #000;
  background: transparent !important; /* ← Asegura que no cambie */
  color: #000; /* texto visible */
  outline: none;
  -webkit-box-shadow: none !important; /* Safari */
  box-shadow: none !important; /* Chrome/Firefox */
  caret-color: #00aaff; /* color del cursor opcional */
}
input:-webkit-autofill {
  transition: background-color 100000s ease-in-out 0s !important;
  -webkit-text-fill-color: #000 !important;
}
input::placeholder {
  color: transparent;
}
label {
  position: absolute;
  left: 8px;
  top: 12px;
  font-size: 18px;
  color: #000;
  font-family: "DmSans-Regular", sans-serif;
  pointer-events: none;
  transition: 0.2s ease all;
}
input:focus + label,
input:not(:placeholder-shown) + label {
  top: -10px;
  left: 8px;
  font-size: 14px;
  color: #00aaff;
}
